import React, { Component } from 'react';
import Modal from 'react-bootstrap/Modal'
import { Button } from 'react-bootstrap';

class EditModal extends Component {
    state = { 
        content: this.props.todo.content
    }

    handleChange = (e) => {
        //Clone
        let state = {...this.state};

        //Edit
        state.content = e.currentTarget.value;

        //Set state
        this.setState(state);
    }
    
    render() { 
        //Making ...rest here because of: https://reactjs.org/warnings/unknown-prop.html
        const {onHide, handleEdit, todo, ...rest} = this.props;
        return ( 
            <Modal
                {...rest}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Edit
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <input type="text" value={this.state.content} className="form-control" onChange={this.handleChange}/>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={onHide}>Close</Button>
                    <Button variant="primary" onClick={()=>{handleEdit(todo, this.state.content); onHide();}}>Edit</Button>
                </Modal.Footer>
            </Modal>
         );
    }
}
 
export default EditModal;