import React, { Component } from 'react';

class AddTodo extends Component {
    state = {
        content: '',
        highPriorityCheck: false
    };

    handleChange = (e) => {
        //Clone
        let state = {...this.state};

        //Edit
        state[e.currentTarget.name] = e.currentTarget.type === 'checkbox'? e.currentTarget.checked : e.currentTarget.value;

        //Set state
        this.setState(state);
    }

    handleClick = () => {
        //Clone
        let state = {...this.state};

        if(!state.content) {
            alert('Please, Write a todo to add');
        } else{ 
            this.props.handleAdd(state);
            
            //Edit
            state = {
                content: '',
                highPriorityCheck: false
            };

            //Set state
            this.setState(state);
        }
    }

    render() { 
        return ( 
            <div className="add-div">
                <div className="add-div-inside">
                    <input name='content' className="form-control" onChange={this.handleChange} type="text" value={this.state.content} placeholder="Add your todo"/>
                    <div>
                        <label><input name='highPriorityCheck' type="checkbox" onChange={this.handleChange} checked={this.state.highPriorityCheck} className="high-priority-checkbox"/> High priority</label>
                        <button onClick={this.handleClick} className="btn btn-primary"><i className="fas fa-plus"></i> Add todo</button>
                    </div>
                </div>
            </div>
         );
    }
}
 
export default AddTodo;