import React, { useState } from 'react';
import EditModal from './editModal';
import { Button } from 'react-bootstrap';

const Todo = (props) => {
    const [modalShow, setModalShow] = useState(false);
    return ( 
        <div className="todo">
            <div className="todo-inside">
                <div className={`todo-content text-left shadow-sm ${props.todo.highPriority? 'high-priority-div' : 'low-priority-div'}`}>
                    <div className={`todo-content-value ${props.todo.completed? 'completed':''}`}>
                        <span>{props.todo.content}</span>
                    </div>
                </div>
                <div>
                    <button onClick={() => {props.handleDone(props.todo)}} className={`btn btn-sm ${props.todo.completed? 'btn-success':'btn-outline-success'}`} title="Done">
                        <i className={"fas fa-check"}></i>
                    </button>

                    <Button variant="warning" onClick={() => setModalShow(true)} size="sm" title="Edit">
                        <i className="fas fa-pen"></i>
                    </Button>
                    <EditModal todo={props.todo} handleEdit={props.handleEdit} show={modalShow} onHide={() => setModalShow(false)} />
                    
                    <button onClick={() => {props.handleDelete(props.todo)}} className="btn btn-sm btn-danger" title="Delete">
                        <i className="fas fa-trash-alt"></i>
                    </button>
                </div>
            </div>
        </div>
    );
}
 
export default Todo;