import React from 'react';

const Quote = (props) => {
    return ( 
        <div className="quote-box shadow p-3 rounded">
            <div className='text-left'>
                <i className="fa fa-quote-left"></i> <span>{props.quotes[props.index].quote}</span>
            </div>
            <hr/>
            <div className='text-right'>
                - <span>{props.quotes[props.index].author}</span>
            </div>  
        </div>
     );
}
 
export default Quote;