import React, { Component } from 'react';
import AddTodo from './addTodo';
import Todo from './todo';
import Quote from './quote';
import axios from'axios';

class App extends Component {
  state = { 
      todoList: [],
      quotes: [''],
      index: 0
  }

  async componentDidMount(){
    const {data} = await axios.get('https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json');
    this.setState({
        quotes: data.quotes,
        index: Math.floor(Math.random() * (data.quotes.length))
    });
    setInterval(() => {
        this.setState({
            index: Math.floor(Math.random() * (data.quotes.length))
        });
    }, 8000);
  }

  handleAdd = (todo) => {
    //Clone
    let todoList = [...this.state.todoList];
    if(todoList.find(e => e.content.toLowerCase() === todo.content.toLowerCase())) {
        alert('You have this todo. Please write a new one');
    } else {
        //New Id not repeated
        const newId = todoList.length === 0? 1 : Math.max(...todoList.map(e => e.id)) + 1;
        
        //Edit
        todoList.push({
            id:  newId,
            content: todo.content,
            completed: false,
            highPriority: todo.highPriorityCheck
        });
        
        todoList.sort( (a,b) => b.highPriority - a.highPriority);

        //Set state
        this.setState({todoList});
    }
  }

  handleDelete = (todo) => {
    //Clone
    let todoList = [...this.state.todoList];

    //Edit
    todoList = todoList.filter(e => e.id !== todo.id);

    //Set state
    this.setState({todoList});
  }

  handleEdit = (todo, newContent) => {
    //Clone
    let todoList = [...this.state.todoList];
    const index = todoList.indexOf(todo);

    if(!newContent) {
        alert('Please, Write a todo to add');
    } else if(todo.content === newContent) {
        // Do nothing
    } else if(todoList.find(e => e.content.toLowerCase() === newContent.toLowerCase())) {
        alert('You have this todo. Please write a new one');
    } else {
        //Edit
        todoList[index].content = newContent;

        //Set state
        this.setState({todoList});
    }
  }

  handleDone = (todo) => {
    //Clone
    let todoList = [...this.state.todoList];
    const index = todoList.indexOf(todo);

    //Edit
    todoList[index].completed = !todoList[index].completed;

    //Set state
    this.setState({todoList});
  }

  handleClearAll = () => {
    //Clone
    let todoList = [...this.state.todoList];

    //Edit
    todoList = [];

    //Set state
    this.setState({todoList});
  }

  handleClearCompleted = () => {
    //Clone
    let todoList = [...this.state.todoList];

    //Edit
    todoList = todoList.filter(e => !e.completed)

    //Set state
    this.setState({todoList});
  }

  render() { 
    return (
      <div className="container-fluid text-center">
        <div className="title">
            <h2><i className="fas fa-list"></i> ToDo List App</h2>
        </div>
        <div className="todos-container">
            <AddTodo handleAdd={this.handleAdd}/>
            <hr/>
            {
                !this.state.todoList.length? (<h5 className="todos-empty-title">Add your first todo</h5>) : this.state.todoList.map((todo)=>{
                    return <Todo key={todo.id} todo={todo} handleEdit={this.handleEdit} handleDelete={this.handleDelete} handleDone={this.handleDone}/>
                })
            }
            <div className="todos-container-buttons">
                <button onClick={this.handleClearAll} disabled={!this.state.todoList.length} className=" btn btn-danger delete-all-todos"><i className="fas fa-trash-alt"></i> Clear all</button>
                <button onClick={this.handleClearCompleted} disabled={!this.state.todoList.find(e => e.completed)} className="btn btn-info hide-completed"><i className="fas fa-times"></i> Clear completed</button>
            </div>
            <hr/>
            <Quote quotes={this.state.quotes} index={this.state.index} />
        </div>
      </div>
     );
  }
}
 
export default App;

